import Swiper from './libs/swiper.js';

document.addEventListener('DOMContentLoaded', () => {

    const sexForm = document.querySelector('.start_sex__form');
    const male = document.querySelector('.start_sex__form_male');
    const female = document.querySelector('.start_sex__form_female');

    function toggleF() {
        let input = female.querySelector('input');
        input.setAttribute('checked', 'checked');
        sexForm.setAttribute('data-sex', 'female');
        if(input.getAttribute('checked') == 'checked') {
            female.classList.add('sex-active');

            if(male.classList.contains('sex-active')) {
                male.classList.remove('sex-active');
                male.querySelector('input').removeAttribute('checked');
            }
        }
    }

    function toggleM() {
        let input = male.querySelector('input');
        input.setAttribute('checked', 'checked');
        sexForm.setAttribute('data-sex', 'male');
        if(input.getAttribute('checked') == 'checked') {
            male.classList.add('sex-active');

            if(female.classList.contains('sex-active')) {
                female.classList.remove('sex-active');
                female.querySelector('input').removeAttribute('checked');
            }
        }
    }

    if(male) {
        male.addEventListener('click', toggleM);
    }
    if(female) {
        female.addEventListener('click', toggleF);
    }


    /*Height swiper*/
    const swiperHeightParam = new Swiper('.scroll_in', {
        direction: 'vertical',
        slidesPerView: 5,
        spaceBetween: 8,
        centeredSlides: true,
        initialSlide: 30,
        slideToClickedSlide: true,


    });


    /*Activity level swiper*/
    const swiperActivityLevel = new Swiper('.q-activity__in', {

        slidesPerView: 1,
        centeredSlides: true,
        initialSlide: 1,
        spaceBetween: -20,
        pagination: {
            el: '.swiper-pagination',
            type: 'bullets'
        }


    });


});





